import { random } from 'lodash'
import { batch } from 'react-redux';

import { chooseCategory } from '../categories/categories-actions'
import { setDrink } from '../drinks/drinks-actions'

const appMiddleware = store => next => action => {


    if (action.type === chooseCategory.type) {

        if (action.payload.id === 'random') {

            const drinks = store.getState().drinks
            if (drinks.drinks.length) {

                const idx = random(drinks.drinks.length - 1);
                const drink = drinks.drinks[idx]

                batch(() => {
                    store.dispatch(setDrink(drink))
                })
            }

        }
    }

    next(action);
}

export default appMiddleware;