import React from 'react';
import './drink-ings.scss'

function DrinkIngs_({ drink }) {
    return (<ul className='drink-ings'>
        {drink.ingredients
            .map((val, idx, arr) => {
                return (<li key={drink.id + '-ings' + idx}
                    className='drink-ings'>

                    <span className='drink-ings'>{val}</span>
                </li>)
            })
        }
    </ul>)
}

export default DrinkIngs_