import React from 'react';
import { connect } from 'react-redux';
import { setDrink } from '../drinks-actions'
import DrinksListItem from './DrinksListItem_'
import Drink from '../drink/Drink'
import './drinks-list.scss'

const stateToProps = state => {
    return {
        drink: state.drinks.drink,
        drinks: state.drinks.drinks
    };
};

function dispatchToProps(dispatch) {
    return { showDrink: drink => dispatch(setDrink(drink)) };
}

function DrinksList({ drink, drinks, showDrink }) {
    if (!drinks.length) {
        return <ul className='drinks-list'><li>no drinks</li></ul>
    }

    return (<ul className='drinks-list'>
        {drinks.map(d => {
            const isSelected = d.id === drink.id

            const cssClass = 'drinks-list '
                + (isSelected ? 'drinks-list-selected' : '')

            const drinkToShow = isSelected ? { id: -1 } : d

            return (<li key={d.id}
                className={cssClass}
                onClick={() => showDrink(drinkToShow)}>

                {isSelected
                    ? (<Drink key={d.id + 'd'} />)
                    : (<DrinksListItem key={d.id + 'dli'} drink={d} />)}
            </li>)
        })}
    </ul >)
}

export default connect(stateToProps, dispatchToProps)(DrinksList);
