import React from 'react';
import { connect } from 'react-redux';
import { setDrink } from '../drinks-actions'
import './ingredients.scss'

const stateToProps = state => {
    return {
        ingredients: state.drinks.ingredients
    };
};

function dispatchToProps(dispatch) {
    return { showDrink: drink => dispatch(setDrink(drink)) };
}

function Ingredients({ ingredients }) {
    return (<ul className='ingredients'>
        {ingredients
            .map((val, idx, arr) => {
                return (<li key={val.id}
                    className='ingredients'>

                    <span className='ingredients'>{val.name}</span>
                </li>)
            })
        }
    </ul>)
}

export default connect(stateToProps, dispatchToProps)(Ingredients);