import React from 'react';
import './category-name.scss'

function CategoryName_({ category }) {

    const cat = 'cat-' + category.id;

    return (<h2 className={'category-name ' + cat} >
        <span className={'center category-name ' + cat}>
            {category.name}
        </span>
    </h2>)
}

export default CategoryName_
