import React from 'react'
import './category.scss'
import CategoryAside from './aside/CategoryAside_'
import CategoryAsideCat from './aside/CategoryAsideCat_'
import DrinksAlco from '../../drinks/drinks-alco/DrinksAlco'
import DrinksCustom from '../../drinks/drinks-custom/DrinksCustom'
import DrinksList from '../../drinks/drinks-list/DrinksList'
import DrinksPrefs from '../../drinks/drinks-prefs/DrinksPrefs'
import DrinksRandom from '../../drinks/drinks-random/DrinksRandom'
import DrinksTop from '../../drinks/drinks-top/DrinksTop'

import { connect, batch } from 'react-redux'
import { showCategories } from '../categories-actions'
import { resetDrinks } from '../../drinks/drinks-actions'

const drinksViews = {
    DrinksAlco,
    DrinksCustom,
    DrinksList,
    DrinksPrefs,
    DrinksRandom,
    DrinksTop
}

const drinksAsides = {
    // TODO
}

const stateToProps = state => {
    const category = state.categories.category
    const drinksViewName = 'Drinks' + category.id[0].toUpperCase() + category.id.slice(1);

    return {
        category,
        DrinksView: drinksViews[drinksViewName],
        AsideContent: drinksAsides[drinksViewName + 'Aside'] || CategoryAsideCat
    };
};

function dispatchToProps(dispatch) {
    return {
        showCategories: () => {
            batch(() => {
                dispatch(resetDrinks())
                dispatch(showCategories())
            })
        }
    };
}

function Category({ category, showCategories, DrinksView, AsideContent }) {

    const catCssClass = 'category-aside-' + category.aside

    return (<div className={'category ' + catCssClass}>

        <DrinksView />

        <CategoryAside category={category}
            cssClassSuffix={category.aside}
            showCategories={showCategories} >

            <AsideContent category={category} />

        </CategoryAside>

    </div>)
}

export default connect(stateToProps, dispatchToProps)(Category);
